.signup-form {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 24px;
  }
  
  .navbar-logo {
    width: 100%;
    text-align: center;
    color: #000000;
    justify-content: center;
    top: 50%;
    left: 50%;
    font-weight: 800;
    margin-bottom: 1.5cm;

  }
  
  .navbar-logo span {
    font-size: 66px; 
    text-shadow: -0.08em 0.03em 0.12em rgb(255, 255, 255);
  }
  
  .navbar-logo span:not(:first-child) {
    margin-left: -0.18em;
  }
  .logo {
    width: 100%;
    max-width: 340px;
    height: 90px;
    object-fit: cover;
  }
  
  .form-container {
    width: 100%;
    max-width: 500px;
  }
  
  .form-title {
    text-align: center;
    margin-bottom: 24px;
  }
  
  .form-subtitle {
    color: #000;
    font-size: 18px;
    font-weight: 400;
    line-height: 150%;
  }
  
  .form {
    display: flex;
    flex-direction: column;
    margin-bottom: 24px;
  }
  
  .form-field {
    margin-bottom: 16px;
  }
  
  .form-label {
    color: #000;
    font-weight: 500;
    margin-bottom: 8px;
  }
  
  .form-input {
    width: 100%;
    height: 42px;
    padding: 8px;
    border: 1px solid #000;
    font-size: 14px;
  }
  
  .form-button {
    width: 100%;
    height: 42px;
    background-color: #000;
    color: #fff;
    border: none;
    font-size: 14px;
    font-weight: 500;
    cursor: pointer;
  }
  
  .form-text {
    text-align: center;
    margin-bottom: 8px;
    font-size: 12px;
  }
  
  .visitor-button {
    background-color: #000;
    color: #fff;
    border: none;
    font-size: 14px;
    font-weight: 500;
    cursor: pointer;
  }